import React, { FC } from "react"
import { StandardLayout } from "../components/layout"
import { Container } from "reactstrap"
import { PageHeader } from "../components/layout/page-header"
import { graphql, PageProps } from "gatsby"
import { FileSystemNode } from "gatsby-source-filesystem"
import { ChildImageSharp, SiteMetadata, ChildMarkdownRemark } from "src/util"

export const pageQuery = graphql`
  query PrivacyQuery {
    privacy: file(relativePath: { eq: "text/privacy.md" }) {
      childMarkdownRemark {
        html
      }
    }
  }
`

type Data = {
  privacy: FileSystemNode & ChildMarkdownRemark
}

const Page: FC<PageProps<Data>> = ({ data }) => {
  return (
    <StandardLayout>
      <Container tag="main">
        <PageHeader pageTitle="Privacy Policy" />
        <article
          dangerouslySetInnerHTML={{
            __html: data.privacy.childMarkdownRemark.html,
          }}
        />
      </Container>
    </StandardLayout>
  )
}

export default Page
